.btnShow {
    background-color: #aae !important;
}
.btnShow:hover {
    background-color: #aea !important;
}
.taskWrapper {
    padding-top: 5px;
}
.singleTask {
    height: 50px;
}
.taskValue {
    width: calc(100% - 300px);
    height: 100%;
    padding: 5px 10px;
    box-sizing: border-box;
    float: left;
    vertical-align: middle;
    line-height: 40px;
}
.controls {
    padding: 7px 5px;
    height: 100%;
    box-sizing: border-box;
    background-color: #ffdab9;
}
.controls.isUpdating {
    width: 316px;
    margin-left: calc(100% - 316px);
}
.controls.notUpdating{
    width: 300px;
    margin-left: calc(100% - 300px);
}
.inputWrapper {
    width: 433px;
    height: 100%;
    box-sizing: border-box;
    float: left;
}
.updateTaskField {
    position: absolute;
    width: 420px;
    height: 35px;
    margin-left: 5px;
    padding: 5px 7px;
    box-sizing: border-box;
    float: left;
    vertical-align: middle;
    line-height: 40px;
}

.btn {
    width: 90px !important;
    font-size: 10pt !important;
}
.btn:disabled {
    background-color: #88a !important;
    color: #fff !important;
}

.btnDone {
    background-color: #5b5 !important;
}
.btnDone:hover {
    background-color: #aea !important;
}

.btnUpdateBegin {
    margin: 0 10px !important;
    background-color: #aae !important;
}
.btnUpdateBegin:hover {
    background-color: #7c9 !important;
}

.btnRemove {
    color: #fff !important;
    background-color: #a52a2a !important;
}
.btnRemove:hover {
    background-color: #f99 !important;
}

.btnBig {
    width: 148px !important;
}

.btnUpdateSave {
    background-color: #5b5 !important;
}
.btnUpdateSave:hover {
    background-color: #aea !important;
}

.btnUpdateCancel {
    margin-left: 10px !important;
    color: #fff !important;
    background-color: #a52a2a !important;
}
.btnUpdateCancel:hover {
    background-color: #f99 !important;
}

.btnRemoveDone {
    width: 290px !important;
    margin-left: 0px !important;
    color: #fff !important;
    background-color: #a52a2a !important;
}
.btnRemoveDone:hover {
    background-color: #f99 !important;
}


.controls.isUpdating .btnSmall {
    width: 95px !important;
}
.controls.isUpdating .btnDone {
    margin-left: -10px !important;
}
.controls.isUpdating .btnRemoveDone {
    width: 304px !important;
    margin-left: -10px !important;
}
.todoList {
    width: 750px;
    height: 435px;
    background-color: #eae;
    border: 2px solid #777;
}
.todoList .taskWrapper:first-child {
    padding-top: 0;
}
.actionsBlock {
    margin-top: 10px;
}
.addTaskField {
    width: 618px;
    height: 23px;
    padding: 5px 8px;
}

.btnAddNew {
    width: 105px !important;
    margin: -2px 0 0 10px !important;
    font-size: 10pt !important;
    color: #fff !important;
    background-color: #cd853f !important;
}
.btnAddNew:hover {
    background-color: #dAa520 !important;
}
.btnAddNew:disabled {
    color: #333 !important;
    background-color: #ccc !important;
}

body {
	margin: 0;
}
#root {
	background-color: #eee;
	min-height: calc(100vh - 30px);

	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
